body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

main {
  flex-grow: 1;
}

h1, h2, h3, p, li {
  color: #ffffff;
}

section {
  padding: 2rem;
  margin: 1rem 0;
}

nav a {
  margin: 0 1rem;
  color: #333;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}
